'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { collection, doc, writeBatch, query, orderBy, limit, onSnapshot, getDocs, Timestamp, setDoc } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { AnimatePresence, motion } from 'framer-motion';
import {
    Zap, Users, Shield, Activity,
    Search, X, Check, History,
    RefreshCw, Settings, Link as LinkIcon
} from 'lucide-react';

// --- Types ---
interface UserData {
    id: string;
    displayName: string;
    phone?: string;
    exp: number;
    rank: string;
    partyId?: string;
}

interface LogEntry {
    id: string;
    timestamp: any;
    adminName: string;
    action: string;
    details: string;
}

// --- Config ---
const ACTIVITY_LIST = [
    { id: 'gym_visit', label: '1. ‡∏°‡∏≤ Gym / ‡πÉ‡∏™‡πà‡πÄ‡∏™‡∏∑‡πâ‡∏≠ Rank', exp: 1 },
    { id: 'gym_win1', label: '2. ‡∏ä‡∏ô‡∏∞ 2 ‡∏£‡∏≠‡∏ö (‡∏¢‡πà‡∏≠‡∏¢)', exp: 1 },
    { id: 'gym_win2', label: '3. ‡∏ä‡∏ô‡∏∞ 4 ‡∏£‡∏≠‡∏ö (Match)', exp: 2 },
    { id: 'gym_top1', label: '4. ‡∏ó‡∏µ‡πà 1 Gym Tournament', exp: 3 },
    { id: 'quest_photo', label: '5. ‡πÄ‡∏Ñ‡∏ß‡∏™‡∏ñ‡πà‡∏≤‡∏¢‡∏£‡∏π‡∏õ', exp: 1 },
    { id: 'quest_review', label: '6. ‡πÄ‡∏Ñ‡∏ß‡∏™‡∏£‡∏µ‡∏ß‡∏¥‡∏ß‡∏Ñ‡∏π‡πà‡πÅ‡∏Ç‡πà‡∏á', exp: 2 },
    { id: 'shop_support', label: '7. ‡∏ã‡∏∑‡πâ‡∏≠‡∏Ç‡∏≠‡∏á‡∏£‡πâ‡∏≤‡∏ô (>1,500)', exp: 5 },
    { id: 'shop_preorder', label: '8. Pre-order Box', exp: 5 },
];

const RANK_THRESHOLDS = [
    { min: 200, name: 'Legend' }, { min: 120, name: 'Grandmaster' },
    { min: 75, name: 'Diamond' }, { min: 50, name: 'Platinum' },
    { min: 30, name: 'Gold' }, { min: 15, name: 'Silver' },
    { min: 5, name: 'Bronze' }, { min: 0, name: 'Rookie' }
];

export default function AdminControlPanel() {
    const { user, loading } = useAuth();
    const router = useRouter();

    // Data
    const [users, setUsers] = useState<UserData[]>([]);
    const [logs, setLogs] = useState<LogEntry[]>([]);

    // State for Flow
    const [stage, setStage] = useState<'IDLE' | 'ADMIN_NAME' | 'SELECT_ACT' | 'SELECT_USER' | 'CONFIRM' | 'SUCCESS' | 'SYNC_CONFIG'>('IDLE');
    const [menuType, setMenuType] = useState<'ACTIVITY' | 'MANUAL' | 'SYNC' | null>(null);

    // Data Inputs
    const [adminName, setAdminName] = useState('');
    const [selectedAct, setSelectedAct] = useState<any>(null);
    const [manualExp, setManualExp] = useState(0);
    const [manualReason, setManualReason] = useState('');
    const [targetUser, setTargetUser] = useState<UserData | null>(null);
    const [prevAdminName, setPrevAdminName] = useState('');

    // Sync Settings
    const [scriptUrl, setScriptUrl] = useState('');
    const [isSyncing, setIsSyncing] = useState(false);
    const [syncStatus, setSyncStatus] = useState('');

    // Search
    const [searchTerm, setSearchTerm] = useState('');
    const [isProcessing, setIsProcessing] = useState(false);

    // --- Init ---
    useEffect(() => {
        if (!loading) {
            if (!user) router.push('/');
            else {
                const savedName = localStorage.getItem('adminName');
                const savedUrl = localStorage.getItem('scriptUrl');
                if (savedName) setPrevAdminName(savedName);
                if (savedUrl) setScriptUrl(savedUrl);

                getDocs(query(collection(db, 'users'))).then(snap => {
                    setUsers(snap.docs.map(d => ({ id: d.id, ...d.data() } as UserData)));
                });

                const unsub = onSnapshot(query(collection(db, 'admin_logs'), orderBy('timestamp', 'desc'), limit(10)), (snap) => {
                    setLogs(snap.docs.map(d => ({ id: d.id, ...d.data() } as LogEntry)));
                });
                return () => unsub();
            }
        }
    }, [user, loading, router]);

    // --- Logic ---
    const calculateRank = (exp: number) => RANK_THRESHOLDS.find(r => exp >= r.min)?.name || 'Rookie';

    const startFlow = (type: 'ACTIVITY' | 'MANUAL') => {
        setMenuType(type);
        setAdminName(prevAdminName);
        setStage(prevAdminName ? 'SELECT_ACT' : 'ADMIN_NAME');
        if (type === 'MANUAL') setSelectedAct({ label: 'Manual Adjustment', exp: 0 });
    };

    const handleNextStep = () => {
        if (stage === 'ADMIN_NAME') {
            if (!adminName.trim()) return alert('‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡∏Å‡∏£‡∏≠‡∏Å‡∏ä‡∏∑‡πà‡∏≠‡πÅ‡∏≠‡∏î‡∏°‡∏¥‡∏ô');
            localStorage.setItem('adminName', adminName);
            setPrevAdminName(adminName);
            setStage(menuType === 'ACTIVITY' ? 'SELECT_ACT' : 'SELECT_USER');
        }
        else if (stage === 'SELECT_ACT') {
            if (!selectedAct) return alert('‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡πÄ‡∏•‡∏∑‡∏≠‡∏Å‡∏Å‡∏¥‡∏à‡∏Å‡∏£‡∏£‡∏°');
            setStage('SELECT_USER');
        }
        else if (stage === 'SELECT_USER') {
            if (!targetUser) return alert('‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡πÄ‡∏•‡∏∑‡∏≠‡∏Å‡∏™‡∏°‡∏≤‡∏ä‡∏¥‡∏Å');
            setStage('CONFIRM');
        }
    };

    const handleExecute = async () => {
        if (!targetUser) return;
        setIsProcessing(true);

        try {
            const batch = writeBatch(db);
            const timestamp = Timestamp.now();

            let expChange = 0;
            let details = '';

            if (menuType === 'ACTIVITY') {
                expChange = selectedAct.exp;
                details = `Activity: ${selectedAct.label}`;
            } else if (menuType === 'MANUAL') {
                expChange = manualExp;
                details = `Manual: ${manualReason}`;
            }

            const newExp = Math.max(0, targetUser.exp + expChange);
            const newRank = calculateRank(newExp);
            const userRef = doc(db, 'users', targetUser.id);

            batch.update(userRef, { exp: newExp, rank: newRank });

            const logRef = doc(collection(db, 'admin_logs'));
            batch.set(logRef, {
                timestamp,
                adminName: adminName || 'Unknown',
                action: menuType === 'ACTIVITY' ? 'Add EXP' : 'Manual Edit',
                details: `${targetUser.displayName} (${expChange > 0 ? '+' : ''}${expChange} EXP) - ${details}`,
                targetId: targetUser.id
            });

            await batch.commit();
            setStage('SUCCESS');
            setTimeout(() => {
                setStage('IDLE');
                setTargetUser(null);
                setSelectedAct(null);
                setManualExp(0);
                setSearchTerm('');
            }, 1500);

        } catch (e) {
            alert('Error: ' + e);
        } finally {
            setIsProcessing(false);
        }
    };

    const handleSync = async () => {
        if (!scriptUrl) return alert('‡∏Å‡∏£‡∏∏‡∏ì‡∏≤‡πÉ‡∏™‡πà App Script URL ‡πÉ‡∏ô‡∏ï‡∏±‡πâ‡∏á‡∏Ñ‡πà‡∏≤‡∏Å‡πà‡∏≠‡∏ô');
        setIsSyncing(true);
        setSyncStatus('Connecting to Sheet...');

        try {
            // 1. Fetch from App Script
            // App Script must deploy as accessible to 'Anyone'
            // URL format: https://script.google.com/macros/s/.../exec?func=getLeaderboard
            const targetUrl = scriptUrl.includes('func=') ? scriptUrl : `${scriptUrl}?func=getLeaderboard`;
            const res = await fetch(targetUrl);
            const data = await res.json();

            if (!Array.isArray(data)) throw new Error('Format ‡∏ú‡∏¥‡∏î‡∏û‡∏•‡∏≤‡∏î (‡∏ï‡πâ‡∏≠‡∏á‡πÄ‡∏õ‡πá‡∏ô Array)');

            setSyncStatus(`Found ${data.length} users. Updating DB...`);

            // 2. Update Firestore
            const batch = writeBatch(db);
            let count = 0;

            for (const row of data) {
                // Expect keys: Codename, EXP, Coins from script
                // Or map columns if the script returns array
                const uid = row['Phone'] || row['Codename'] || `u_${Math.random()}`; // Fallback ID
                const userRef = doc(db, 'users', String(uid)); // Use Phone as ID if possible

                const exp = Number(row['EXP'] || 0);
                const rank = calculateRank(exp);

                batch.set(userRef, {
                    displayName: row['Codename'] || 'Unknown',
                    exp: exp,
                    coins: Number(row['G'] || row['Coins'] || 0),
                    phone: row['Phone'] || '',
                    rank: rank,
                    partyId: row['Party'] || null
                }, { merge: true });

                count++;
            }

            await batch.commit();
            setSyncStatus(`Success! Updated ${count} users.`);
            setTimeout(() => { setIsSyncing(false); setSyncStatus(''); setStage('IDLE'); }, 2000);

        } catch (e: any) {
            console.error(e);
            setSyncStatus('Failed: ' + e.message);
            setIsSyncing(false);
        }
    };

    if (loading) return <div className="h-screen bg-slate-900 grid place-items-center text-white">Loading...</div>;

    return (
        <div className="min-h-screen bg-slate-900 text-slate-100 font-sans pb-20">

            {/* Navbar */}
            <div className="bg-slate-800 p-4 shadow-md flex justify-between items-center sticky top-0 z-10">
                <h1 className="font-bold text-lg flex items-center gap-2">
                    <Shield className="text-blue-500" /> Admin Panel
                </h1>
                <button onClick={() => router.push('/dashboard')} className="text-sm text-slate-400 font-bold hover:text-white">EXIT</button>
            </div>

            {/* Main Grid Menu */}
            <div className="max-w-md mx-auto p-6 space-y-6">

                {prevAdminName && (
                    <div className="flex justify-between items-center bg-slate-800/50 p-3 rounded-lg border border-slate-700">
                        <div className="text-xs text-slate-400">Current Admin:</div>
                        <div className="font-bold text-blue-400 flex items-center gap-2">
                            {prevAdminName}
                            <button onClick={() => { setPrevAdminName(''); setAdminName(''); localStorage.removeItem('adminName'); }} className="p-1 hover:bg-slate-700 rounded"><X className="w-3 h-3 text-slate-500" /></button>
                        </div>
                    </div>
                )}

                <div className="grid grid-cols-1 gap-4">
                    <button onClick={() => startFlow('ACTIVITY')} className="h-24 bg-gradient-to-br from-blue-600 to-blue-800 rounded-2xl flex items-center justify-center gap-3 shadow-lg active:scale-95 transition-transform border border-blue-500/20">
                        <Zap className="w-8 h-8 text-white" />
                        <div className="text-left text-white">
                            <div className="font-bold text-lg">‡πÄ‡∏û‡∏¥‡πà‡∏° EXP ‡∏Å‡∏¥‡∏à‡∏Å‡∏£‡∏£‡∏°</div>
                            <div className="text-xs opacity-70">Add Activity EXP</div>
                        </div>
                    </button>

                    <button onClick={() => startFlow('MANUAL')} className="h-20 bg-slate-800 border border-slate-700 rounded-xl flex items-center justify-center gap-3 hover:bg-slate-750 active:scale-95 transition-transform p-4">
                        <Activity className="w-6 h-6 text-green-400" />
                        <div className="text-left">
                            <div className="font-bold">‡πÄ‡∏û‡∏¥‡πà‡∏° EXP Manual</div>
                            <div className="text-xs text-slate-500">‡∏Å‡∏£‡∏ì‡∏µ‡∏û‡∏¥‡πÄ‡∏®‡∏© / ‡πÅ‡∏Å‡πâ‡πÑ‡∏Ç</div>
                        </div>
                    </button>

                    <button onClick={() => setStage('SYNC_CONFIG')} className="h-20 bg-slate-800 border border-slate-700 rounded-xl flex items-center justify-center gap-3 hover:bg-slate-750 active:scale-95 transition-transform p-4 relative overflow-hidden group">
                        <div className="absolute inset-0 bg-yellow-500/5 translate-y-full group-hover:translate-y-0 transition-transform"></div>
                        <RefreshCw className={`w-6 h-6 text-yellow-400 ${isSyncing ? 'animate-spin' : ''}`} />
                        <div className="text-left flex-1">
                            <div className="font-bold text-yellow-100">Sync Google Sheet</div>
                            <div className="text-xs text-slate-500">{syncStatus || '‡∏î‡∏∂‡∏á‡∏Ç‡πâ‡∏≠‡∏°‡∏π‡∏•‡∏™‡∏°‡∏≤‡∏ä‡∏¥‡∏Å / ‡∏≠‡∏±‡∏û‡πÄ‡∏î‡∏ó'}</div>
                        </div>
                    </button>
                </div>

                {/* Logs */}
                <div className="mt-8">
                    <h3 className="text-sm font-bold text-slate-500 uppercase mb-3 flex items-center gap-2">
                        <History className="w-4 h-4" /> Recent Logs
                    </h3>
                    <div className="space-y-2">
                        {logs.map(log => (
                            <div key={log.id} className="text-xs bg-slate-800 p-3 rounded border-l-2 border-blue-500">
                                <div className="flex justify-between text-slate-500 mb-1">
                                    <span>{log.adminName}</span>
                                    <span>{log.timestamp?.toDate().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</span>
                                </div>
                                <div className="text-slate-300">{log.details}</div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {/* --- MODALS --- */}
            <AnimatePresence>

                {/* SYNC CONFIG MODAL */}
                {stage === 'SYNC_CONFIG' && (
                    <div className="fixed inset-0 bg-black/90 z-50 flex items-center justify-center p-4">
                        <motion.div initial={{ scale: 0.9 }} animate={{ scale: 1 }} className="bg-slate-800 w-full max-w-sm rounded-2xl border border-slate-700 p-6 space-y-4">
                            <h3 className="font-bold text-white flex items-center gap-2"><Settings className="w-5 h-5" /> Sync Settings</h3>

                            <div className="space-y-2">
                                <label className="text-xs font-bold text-slate-400">App Script Web App URL</label>
                                <input
                                    type="text"
                                    value={scriptUrl}
                                    onChange={e => { setScriptUrl(e.target.value); localStorage.setItem('scriptUrl', e.target.value); }}
                                    className="w-full bg-slate-900 border border-slate-600 rounded p-3 text-xs text-white break-all"
                                    placeholder="https://script.google.com/macros/s/.../exec"
                                />
                                <p className="text-[10px] text-slate-500 leading-relaxed">
                                    *‡∏ï‡πâ‡∏≠‡∏á Deploy App Script ‡πÄ‡∏õ‡πá‡∏ô <u>Web App</u> ‡∏Å‡πà‡∏≠‡∏ô<br />
                                    *‡πÄ‡∏•‡∏∑‡∏≠‡∏Å "Who has access" ‡πÄ‡∏õ‡πá‡∏ô <u>Anyone</u>
                                </p>
                            </div>

                            <div className="flex gap-3 pt-2">
                                <button onClick={() => setStage('IDLE')} disabled={isSyncing} className="flex-1 py-2 text-slate-400 font-bold bg-slate-700/50 rounded-lg">Close</button>
                                <button onClick={handleSync} disabled={isSyncing || !scriptUrl} className="flex-1 py-2 bg-yellow-600 text-white font-bold rounded-lg flex items-center justify-center gap-2">
                                    {isSyncing ? 'Syncing...' : 'Start Sync'}
                                </button>
                            </div>

                            {syncStatus && <div className="text-center text-xs text-yellow-300 font-mono mt-2">{syncStatus}</div>}
                        </motion.div>
                    </div>
                )}

                {/* WIZARD MODAL (Name -> Activity -> User -> Confirm) */}
                {['ADMIN_NAME', 'SELECT_ACT', 'SELECT_USER', 'CONFIRM'].includes(stage) && (
                    <div className="fixed inset-0 bg-black/90 z-50 flex items-end md:items-center justify-center p-4 backdrop-blur-sm">
                        <motion.div
                            initial={{ y: 100, opacity: 0 }}
                            animate={{ y: 0, opacity: 1 }}
                            className="bg-slate-800 w-full max-w-sm rounded-2xl border border-slate-700 shadow-2xl overflow-hidden mb-10 md:mb-0"
                        >
                            <div className="p-4 border-b border-slate-700 flex justify-between items-center bg-slate-900/50">
                                <h3 className="font-bold text-white">
                                    {stage === 'ADMIN_NAME' && 'üë§ Step 1: Admin Name'}
                                    {stage === 'SELECT_ACT' && '‚ö° Step 2: Select Activity'}
                                    {stage === 'SELECT_USER' && 'üîç Step 3: Select User'}
                                    {stage === 'CONFIRM' && '‚ö†Ô∏è Confirm Action'}
                                </h3>
                                <button onClick={() => setStage('IDLE')}><X className="w-5 h-5 text-slate-400" /></button>
                            </div>

                            <div className="p-6 max-h-[60vh] overflow-y-auto">
                                {stage === 'ADMIN_NAME' && (
                                    <input
                                        autoFocus type="text" value={adminName} onChange={e => setAdminName(e.target.value)}
                                        onKeyDown={e => e.key === 'Enter' && handleNextStep()}
                                        className="w-full bg-slate-900 border border-slate-600 rounded-lg p-3 text-white" placeholder="Name..."
                                    />
                                )}

                                {stage === 'SELECT_ACT' && (
                                    <div className="space-y-2">
                                        {menuType === 'ACTIVITY' ? ACTIVITY_LIST.map(act => (
                                            <button key={act.id} onClick={() => { setSelectedAct(act); setTimeout(() => setStage('SELECT_USER'), 100); }}
                                                className="w-full text-left p-3 rounded-lg bg-slate-700 hover:bg-blue-600 flex justify-between group">
                                                <span className="text-sm font-bold text-white">{act.label}</span>
                                                <span className="text-xs bg-slate-900/50 px-2 py-1 rounded">+{act.exp}</span>
                                            </button>
                                        )) : (
                                            <div className="space-y-3">
                                                <input type="number" autoFocus value={manualExp} onChange={e => setManualExp(Number(e.target.value))} placeholder="Exp Amount" className="w-full bg-slate-900 border border-slate-600 rounded p-3 text-white font-bold" />
                                                <input type="text" value={manualReason} onChange={e => setManualReason(e.target.value)} placeholder="Reason" className="w-full bg-slate-900 border border-slate-600 rounded p-3 text-white" />
                                            </div>
                                        )}
                                    </div>
                                )}

                                {stage === 'SELECT_USER' && (
                                    <div>
                                        <div className="relative mb-3">
                                            <Search className="absolute left-3 top-3 text-slate-500 w-4 h-4" />
                                            <input type="text" autoFocus value={searchTerm} onChange={e => setSearchTerm(e.target.value)}
                                                className="w-full bg-slate-900 border border-slate-600 rounded-lg pl-10 p-3 text-white" placeholder="Search..." />
                                        </div>
                                        <div className="space-y-1 h-48 overflow-y-auto">
                                            {users.filter(u => u.displayName.toLowerCase().includes(searchTerm.toLowerCase())).map(u => (
                                                <button key={u.id} onClick={() => { setTargetUser(u); setTimeout(() => setStage('CONFIRM'), 50); }}
                                                    className="w-full text-left p-2 rounded hover:bg-blue-600 flex justify-between items-center bg-slate-700/30 mb-1">
                                                    <span className="text-sm font-bold text-white">{u.displayName}</span>
                                                    <span className="text-xs font-mono text-slate-400">{u.exp} EXP</span>
                                                </button>
                                            ))}
                                        </div>
                                    </div>
                                )}

                                {stage === 'CONFIRM' && targetUser && (
                                    <div className="text-center space-y-4">
                                        <div className="bg-slate-900 p-4 rounded-xl border border-slate-700">
                                            <div className="text-blue-400 font-bold text-lg">{menuType === 'ACTIVITY' ? selectedAct.label : manualReason}</div>
                                            <div className="text-2xl font-black text-white mt-1">{(menuType === 'ACTIVITY' ? selectedAct.exp : manualExp) > 0 ? '+' : ''}{(menuType === 'ACTIVITY' ? selectedAct.exp : manualExp)} EXP</div>
                                        </div>
                                        <div className="text-slate-400 text-sm">Target: <b className="text-white">{targetUser.displayName}</b></div>
                                    </div>
                                )}
                            </div>

                            {/* FOOTER */}
                            <div className="p-4 border-t border-slate-700 bg-slate-900/50 flex gap-3">
                                {stage === 'CONFIRM' ? (
                                    <button onClick={handleExecute} disabled={isProcessing} className="w-full py-3 bg-blue-600 text-white font-bold rounded-lg shadow-lg">
                                        {isProcessing ? 'Processing...' : 'CONFIRM'}
                                    </button>
                                ) : (
                                    <button onClick={handleNextStep} className="w-full py-3 bg-blue-600 text-white font-bold rounded-lg">NEXT &raquo;</button>
                                )}
                            </div>
                        </motion.div>
                    </div>
                )}

                {/* SUCCESS */}
                {stage === 'SUCCESS' && (
                    <div className="fixed inset-0 z-[60] flex items-center justify-center pointer-events-none">
                        <motion.div initial={{ scale: 0.5, opacity: 0 }} animate={{ scale: 1, opacity: 1 }} exit={{ scale: 1.5, opacity: 0 }}
                            className="bg-green-500 p-6 rounded-3xl shadow-2xl flex flex-col items-center gap-2">
                            <Check className="w-8 h-8 text-white" strokeWidth={4} />
                            <div className="text-white font-black text-xl">SUCCESS!</div>
                        </motion.div>
                    </div>
                )}
            </AnimatePresence>

        </div>
    );
}
